// Fonts
$font-family-base:             "Roboto", sans-serif !default;
$font-family-icon:             "nucleo" !default;

$font-weight-light:            300 !default;
$font-weight-normal:           400 !default;
$font-weight-medium:           500 !default;
$font-weight-bold:             700 !default;
$font-weight-base:             $font-weight-normal !default;

$font-size-base:               1rem !default;
$font-size-lg:                 ($font-size-base * 1.25) !default;
$font-size-sm:                 ($font-size-base * .875) !default;

$h1-font-size:                 1.75rem !default;
$h2-font-size:                 1.5rem !default;
$h3-font-size:                 1.375rem !default;
$h4-font-size:                 1.25rem !default;
$h5-font-size:                 1.125rem !default;
$h6-font-size:                 1rem !default;

$display1-weight:              500 !default;
$display2-weight:              500 !default;
$display3-weight:              500 !default;
$display4-weight:              500 !default;
$display5:                     2rem !default;

// Colors
$white:                        #fff !default;
$black:                        #000 !default;
$blue:                         #2575dc !default;
$purple:                       #6a21e6 !default;
$red:                          #e45454 !default;
$orange:                       #fb9c5d !default;
$green:                        #7fad4a !default;
$gray:                         #67646b !default;

$primary:                      $blue !default;
$danger:                       $red !default;
$warning:                      $orange !default;
$secondary:                    $gray !default;
$light:                        #ebeaec !default;
$dark:                         #2a2b2d !default;
$info:                         #52BAFF !default;

// Social colors
$social-colors: (
  "facebook":                  #3b5999,
  "twitter":                   #55acee,
  "linkedin":                  #0077B5,
  "skype":                     #00AFF0,
  "vk":                        #4c75a3,
  "vimeo":                     #1ab7ea,
  "google-plus":               #dd4b39,
  "youtube":                   #cd201f,
  "reddit":                    #ff5700,
  "pinterest":                 #bd081c,
  "instagram":                 #3F729B,
  "dribbble":                  #ea4c89,
  "twitch":                    #6441a5,
  "steam":                     #111,
  "discord":                   #7289da,
  "paypal":                    #00588B,
  "wordpress":                 #0087be,
  "drupal":                    #0077c0,
  "joomla":                    #5091cd,
  "tumblr":                    #34526F,
  "github":                    #333,
  "soundcloud":                #ff5500,
  "spotify":                   #1db954,
  "slack":                     #e01563,
  "windows":                   #00bcf2,
  "linux":                     #1793d1,
  "ps4":                       $blue,
  "xbox":                      $green
);

$yiq-contrasted-threshold:     200 !default;
$yiq-text-dark:                #757373 !default;

// Link
$link-color:                   inherit !default;
$link-hover-color:             darken($primary, 5%) !default;
$link-hover-decoration:        none !default;

$dropdown-link-color:         #414242 !default;
$zindex-dropdown:              7 !default;

// Heading
$headings-color:               #505050 !default;
$headings-font-weight:         $font-weight-medium !default;

// Lead
$lead-font-size:               18px !default;
$lead-font-weight:             400 !default;

// Body
$body-bg:                      #fafafa !default;
$body-color:                   #59595f !default;
$box-shadow:                   0 2px 3px 0px rgba(0,0,0, .05) !default;

// Navbar
$navbar-dark-color:            rgba($white, .8) !default;
$navbar-dark-hover-color:      $white !default;
$navbar-dark-active-color:     $navbar-dark-hover-color !default;

$navbar-nav-link-padding-x:    10px !default;
$nav-link-padding-y:           0.625rem !default;

// Buttons
$btn-focus-width:              0 !default;
$btn-padding-x:                15px !default;
$btn-font-weight:              $font-weight-normal !default;

$code-color:                   $primary !default;

// Forms
$input-border-color:           #d9d8de !default;
$input-btn-focus-color:        lighten($primary, 40%) !default;
$input-group-addon-bg:         #f7f7f7 !default;
$input-group-addon-color:      #5d5d5d !default;
$input-disabled-bg:            darken($body-bg, 1%) !default;
$breadcrumb-padding-y:         .7rem !default;

$form-text-margin-top:         .725rem !default;
$form-group-margin-bottom:     1.5rem !default;

$custom-control-indicator-bg:  #dadada !default;

// Alerts
$alert-bg-level:               -10 !default;
$alert-border-level:           -7 !default;
$alert-color-level:            4 !default;
$alert-link-font-weight:       500 !default;
$alert-padding-y:              .7rem;

// Progress
$progress-height:              10px !default;
$progress-bg:                  #ebedef !default;

// Badges
$badge-font-size:              70% !default;
$badge-padding-y:              10px !default;
$badge-padding-x:              10px !default;
$badge-pill-padding-x:         15px !default;

// Card
$card-spacer-x:                15px !default;
$card-spacer-img:              10px !default;
$card-cap-bg:                  #fdfdfd !default;

// Tooltips
$tooltip-bg:                   darken($gray, 20%) !default;
$tooltip-opacity:              1 !default;

// Popover
$popover-border-color:         lighten($dark, 65%) !default;

// Owl carousel
$dot-width:				             16px !default;
$dot-height:		            	 16px !default;
$dot-rounded:		             	 30px !default;
$dot-margin: 	            		 5px 3px !default;
$dot-background:	           	 rgba(0,0,0, .15) !default;
$dot-background-active:	       rgba(0,0,0, .5) !default;

$small-font-size:              14px !default;

// Modal
$modal-sm:                     380px !default;

// Pagination
$pagination-focus-box-shadow:           none !default;
$pagination-hover-color:                inherit !default;
$pagination-hover-bg:                   rgba($black, .05) !default;
$pagination-disabled-border-color:    #d5d9de !default;
$pagination-border-color:             #d5d9de !default;
$pagination-hover-border-color:       #d5d9de !default;

$dropdown-min-width:           180px !default;
$dropdown-link-color:          #232323 !default;
$dropdown-item-padding-x:      15px !default;

$breadcrumb-bg:                transparent !default;
$breadcrumb-margin-bottom:     0 !default;
$breadcrumb-padding-x:         0 !default;
$breadcrumb-item-padding:      5px !default;

$input-color:                  #37393c !default;

// Footer
$footer-padding:               4.375rem 0 30px 0 !default;
$footer-bottom-margin-top:     55px !default;

$footer-bg-color:              #2f3133 !default;
$footer-color:                 rgba(255, 255, 255, 0.45) !default;
$footer-hover-color:           #FFF !default;
$footer-font-size:             $font-size-sm !default;

$footer-title-color:           #FFF !default;
$footer-title-font-size:       $font-size-sm !default;
$footer-title-font-weight:     $font-weight-bold !default;
$footer-title-margin-bottom:   20px !default;

$footer-social-font-size:      16px !default;
$footer-social-color:          rgba(255, 255, 255, 0.8) !default;
$footer-social-padding:        0 .375rem !default;
$footer-social-margin:         -5px !default;

// Animation delay
$animation-delay: () !default;
$animation-delay: map-merge((
  1: .2s,
  2: .4s,
  3: .6s,
  4: .8s,
  5: 1s,
  6: 1.2s,
  7: 1.4s,
  8: 1.6s,
  9: 1.8s,
  10: 2s,
), $animation-delay);
